<template>
  <v-container>
    <v-form @submit.prevent="agregaMascota()">
      <v-text-field
        v-model="atributos.nombre"
        label="Nombre"
        
      ></v-text-field>

      <v-text-field
        v-model="atributos.raza"
        label="Raza"
        
      ></v-text-field>

      <v-text-field
        v-model="atributos.edad"
        label="Edad"
        
      ></v-text-field>

      <v-select
        v-model="atributos.especie"
        :items="especieOpc"
        label="Especie"
      ></v-select>

      <v-btn color="success" class="mr-4" type="submit">
        Enviar registro
      </v-btn>

      <v-btn color="error" class="mr-4" > Limpiar</v-btn>

    </v-form>
  </v-container>
</template>

<script>
export default {
  data(){
    return{
      Atributos: [],

      atributos: {
        nombre: "",
        especie: "",
        raza: "",
        edad: ""
      },

      especieOpc:['Gato', 'Perro', 'Aves', 'Reptiles', 'Peces', 'Insectos']
    }
  },

  methods:{

    //Funcion agrega mascotas
    agregaMascota(){
      console.log(this.atributos);
        if(!this.atributos.nombre){
          return this.$swal({
            icon: 'error',
            title: 'Oops...',
            text: 'Ingrese el nombre de la mascota!',
          })
        } else if(!this.atributos.especie){
          return this.$swal({
            icon: 'error',
            title: 'Oops...',
            text: 'Falta ingresar la especie!',
          })
        } else if(!this.atributos.raza){
          return this.$swal({
            icon: 'error',
            title: 'Oops...',
            text: 'Ingrese la raza de la mascota!',
          })
        } else if(!this.atributos.edad){
          return this.$swal({
            icon: 'error',
            title: 'Oops...',
            text: 'Ingrese la edad de la mascota!',
          })
        } else {
          this.axios.post("/nuevo-registro", this.atributos)
            .then((res)=>{
              this.Atributos.push(res.data);
              this.$swal({
                  icon: 'success',
                  title: 'Registro Completo',
                  text: 'Mascota Agregada!'
                }                               
              )
              this.atributos.nombre = "";
              this.atributos.especie = "";
              this.atributos.edad = "";
              this.atributos.raza = "";
            })
            .catch((e)=>{
              console.log(e.response);
              alert("Error al guardar registro, comuniquese con el administrador")
            })
        }      
    }
  }
};
</script>